/* <--------------------------- * SECTION1 * --------------------------- */
.reusableItem_container {
    display: flex;
    align-items: center;
    height: 4rem;
    position: relative;
    background: $background;
    border-bottom: 0.1rem solid $line_light;
}
.reusableItem_dragging {
    background: white;
    box-shadow: $shadow_large;
    border: none !important;
}
.reusableItem_isDragging {
    opacity: 0.3;
}

.reusableItem_button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    width: 7rem;
    height: 4rem;
    line-height: 4rem;
    color: #bdbdbd;
    border-right: 0.1rem solid #bdbdbd;
    padding: 0 1rem;
    cursor: pointer;
    svg {
        font-size: 1.6rem;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
