/* <--------------------------- * SECTION1 * --------------------------- */
.nodeInstruction_textContainer {
    min-height: 16.9rem;
    border-bottom: 0.1rem solid $line_light;
}

.nodeInstruction_hintTitle {
    font: $fs_r_20;
    color: $primary_600;
}

.nodeInstruction_hintContainer {
    display: flex;
    align-items: center;
    padding-right: 2.5rem;
    padding-left: 2.5rem;
    height: 4.9rem;
    border-bottom: 0.1rem dashed $line_light;
    span {
        @include h_4();
        font: $fs_r_20;
        color: $primary_600;
    }
    svg {
        margin-left: 1rem;
        font-size: 1.6rem;
        color: $neutral_200;
    }
}
.nodeInstruction_edit {
    min-height: 15rem;
    > div {
        min-height: 15rem !important;
    }
}
.nodeInstruction_hint {
    .kite_body {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 26.4rem;
        height: 9.6rem !important;
        text-align: left;
        @include body_2();
    }
}
