/* <--------------------------- * SECTION1 * --------------------------- */
.reusableListTree_item {
    display: flex;
    align-items: center;
    padding-left: 3.2rem;
    height: 4rem;
    position: relative;
    box-sizing: border-box;
    font: $fs_r_14;
    color: #4d4d4d;
    cursor: pointer;
    &:hover {
        background: rgba($primary_variant, 0.07);
        transition: 0.2s;
        .reusableListTree_itemDelete {
            display: block;
            color: $neutral_300;
        }
    }
    &__select {
        background: rgba($primary_variant, 0.07);
    }
    svg {
        margin-right: 1rem;
        font-size: 1.6rem;
        color: #22a6b3;
    }
    .reusableListTree_itemDelete {
        display: none;
        position: absolute;
        top: 50%;
        right: 0.5rem;
        font-size: 1.5rem;
        transform: translateY(-50%);
        cursor: pointer;
        &:hover {
            color: $danger_600;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
