/* <--------------------------- * SECTION1 * --------------------------- */
.scriptElement_container {
    display: inline-flex;
    padding: 0.3rem;
    margin: 0 0.5rem;
    width: 25rem;
    height: 3rem;
    box-sizing: border-box;
    background: $background;
    border: 0.1rem solid $neutral_300;
    border-radius: 0.6rem;
    user-select: none;
}
.scriptElement_script {
    position: relative;
    display: flex;
    align-items: center;
    padding: 0 0.5rem;
    width: 11.4rem;
    height: 100%;
    box-sizing: border-box;
    background: $neutral_100;
    border-radius: 0.6rem;
    user-select: none;
    svg {
        font-size: 1.4rem;
        color: $yellow_600;
        cursor: pointer;
        user-select: none;
    }
    .scriptElement_line {
        margin: 0 0.8rem;
        width: 0.1rem;
        height: 1.4rem;
        background: $neutral_300;
        user-select: none;
    }
    .scriptElement_name {
        @include label();
        width: 7.2rem;
        height: 2.2rem;
        line-height: 2.2rem;
        color: $neutral_500;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        cursor: pointer;
        user-select: none;
    }
}
.scriptElement_desc {
    @include label();
    padding: 0 0.4rem;
    margin-left: 0.8rem;
    height: 2.2rem;
    width: 12rem;
    line-height: 2.2rem;
    color: $neutral_500;
    background: $neutral_100;
    border-radius: 0.6rem;
    user-select: none;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.scriptElement_tip {
    .kite_body {
        display: flex;
        align-items: center;
        width: auto;
        height: 3.2rem;
        font-size: 1.2rem;
        white-space: nowrap;
        user-select: none;
        > span {
            line-height: 1.6rem;
            user-select: none;
        }
        svg {
            margin-right: 0.4rem;
            font-size: 1.6rem;
            user-select: none;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
