/* <--------------------------- * SECTION1 * --------------------------- */
.codeInput_container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    min-height: 4rem;
    border-right: 0.1rem solid $line_light;
    box-sizing: border-box;
    input {
        width: 100%;
        height: 100%;
        text-align: center;
        border: none;
        outline: none;
    }
    &::after {
        content: '';
        position: absolute;
        left: -0.1rem;
        top: 0;
        width: 0.1rem;
        height: 100%;
        background: $line_light;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
