/* <--------------------------- * SECTION1 * --------------------------- */
.builtInScriptTree_item {
    display: flex;
    align-items: center;
    padding-left: 3.2rem;
    height: 4rem;
    font: $fs_r_14;
    color: #4d4d4d;
    cursor: pointer;
    svg {
        margin-right: 1rem;
        font-size: 1.6rem;
        color: #22a6b3;
    }
    &:hover {
        background: rgba($primary_variant, 0.07);
        transition: 0.2s;
    }
    &__select {
        background: rgba($primary_variant, 0.07);
    }
}
.builtInScriptTree_name {
    width: calc(100% - 3.5rem);
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
