/* <--------------------------- * SECTION1 * --------------------------- */
.single_radio {
    width: 100%;
    height: auto;
    margin-bottom: 0;
    padding: 0 0.8rem;
    box-sizing: border-box;
    &:hover {
        background: $primary_50;
    }
    &__checked {
        .radio_content {
            color: $primary_600;
        }
    }
    .radio_content {
        width: 100%;
        min-height: 2.1rem;
        padding: 1rem 0;
        font-size: 1.4rem;
    }
}
