/* <--------------------------- * SECTION1 * --------------------------- */
.loopOptions_nodesTag {
    height: 3.4rem;
    line-height: 3.4rem;
    padding-left: 1.8rem;
    border-bottom: 0.1rem solid $line_light;
}
.loopOptions_nodesAdd {
    font: $fs_r_12;
    height: 4rem;
    display: flex;
    align-items: center;
    border-bottom: 0.1rem solid $line_light;
    color: $line_light;
    cursor: pointer;
    > span {
        font-size: 1.8rem;
        color: $primary_600;
        margin-left: 1.3rem;
        margin-right: 0.8rem;
    }
}

.loopOptions_moveGroupItem {
    position: fixed;
    background: $background;
    z-index: 100;
    box-shadow: $shadow_small;
    cursor: pointer;
    pointer-events: none;
    overflow: hidden;
    .questionOptionItem_questionList_item {
        border-bottom: none !important;
    }
    .reusableList_container {
        border-bottom: none !important;
    }
}
.loopOptions_dragArea {
    width: 100%;
    height: 4rem;
    border: 0.2rem dashed #ccc;
    border-radius: 0.2rem;
}
/* <--------------------------- * SECTION1 * --------------------------- */
