/* <--------------------------- * SECTION1 * --------------------------- */
.loopNodeItem_nodesContainer {
    height: 4rem;
    border-bottom: 0.1rem solid $line_light;
}
.loopNodeItem_buttonContainer {
    display: inline-block;
    width: 4.9rem;
    height: 4rem;
    line-height: 4rem;
    color: $line_light;
    border-right: 0.1rem solid $line_light;
    vertical-align: top;
    svg {
        font-size: 1.6rem;
    }
}
.loopNodeItem_dragging {
    background: white;
    box-shadow: $shadow_large;
    border: none !important;
}
.loopNodeItem_isDragging {
    opacity: 0.3;
}
.loopNodeItem_contentContainer {
    display: inline-block;
    width: calc(100% - 5rem);
    vertical-align: top;
    height: 4rem;
    line-height: 4rem;
    position: relative;
    padding-left: 1.3rem;
    box-sizing: border-box;
}
.loopNodeItem_jumpButton {
    font: $fs_m_14;
    color: $background;
    user-select: none;
    text-align: center;
    position: absolute;
    right: 1rem;
    top: 0.7rem;
    width: 11.8rem;
    height: 2.6rem;
    line-height: 2.6rem;
    background: $primary_600;
    border-radius: 0.4rem;
    cursor: pointer;
}
.loopNodeItem_button {
    display: inline-block;
    cursor: pointer;
    margin-left: 0.8rem;
}

.loopNodeItem_button {
    display: inline-block;
    margin-left: 0.8rem;
    cursor: pointer;
}
.loopNodeItem_jumpButton:hover {
    background: $primary_600;
    transition: 0.2s;
}
