/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */


/** Import all the reference constant after this line */

@import '../../Constant/breakPoint.scss';
@import '../../Constant/font.scss';
@import '../../Constant/color.scss';

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

@mixin commonBorder {
    border-radius: 0.4rem;
    filter: drop-shadow( 0px 0px 8px 0px #000000 25%);
}


/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */


/* <------------------------------------ **** COMMON STYLE START **** ------------------------------------ */

.ColorEditor_styleBarSubMenuWrapper {
    @include commonBorder;
    font: $fs_r_10;
    width: 11rem;
    background: $background;
    transition: 0.2s linear;
    box-shadow: $shadow_01dp;
    text-align: left;
    padding: 1rem 0.6rem;
    user-select: none;
    cursor: pointer;
    >div {
        margin-top: 0.8rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    >div:first-child {
        margin-top: 0rem;
    }
}

.ColorEditor_styleBarSubMenuItemWrapper {
    width: 100%;
    height: 2rem;
    color: $line_dark;
    position: relative;
    transition: 0.2s linear;
}

.ColorEditor_styleBarSubMenuItemIcon {
    margin-left: 0.5rem;
    margin-right: 2rem;
}

.ColorEditor_styleBarSubMenuItemContent {
    float: right;
    height: 2rem;
    line-height: 2rem;
    margin-right: 0.5rem;
}

.ColorEditor_hiddenColorSelectWrapper {
    position: absolute;
    height: 2rem;
    width: 11rem;
    background: red;
    top: 0px;
    opacity: 0;
}

.ColorEditor_hiddenColorSelect {
    height: 1.8rem;
    width: 10.5rem;
    cursor: pointer;
}