/* <--------------------------- * SECTION1 * --------------------------- */
.select_selector {
    @include body_2();
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1.6rem;
    width: 100%;
    color: $neutral_400;
    box-sizing: border-box;
    border: 0.8px solid $neutral_200;
    border-radius: 0.6rem;
    cursor: pointer;
    user-select: none;
    &__select {
        border-color: $primary_600;
    }
    .select_dropDown {
        color: $neutral_500;
        transition: transform 0.1s ease;
    }
}
.select_placeholder {
    color: $neutral_300;
}
.select_list {
    padding: 0.5rem 0.4rem;
    width: 100%;
    box-sizing: border-box;
    box-shadow: $shadow_regular;
    border-radius: 0.6rem;
    background: $background;
}
.select_item {
    @include body_2();
    display: flex;
    align-items: center;
    padding-left: 0.8rem;
    margin-bottom: 0.5rem;
    width: 100%;
    height: 2.8rem;
    box-sizing: border-box;
    border-radius: 0.4rem;
    cursor: pointer;
    user-select: none;
    &:hover {
        background: $primary_100;
    }
    &__select {
        color: $primary_600;
        background: $background !important;
    }
}
.select_searchInput {
    display: flex;
    box-sizing: border-box;
    &_input {
        @include body_2();
        flex: 1;
        outline: none;
        padding: 0 1.6rem;
        width: 100%;
        height: 4rem;
        color: $neutral_400;
        box-sizing: border-box;
        border: 0.8px solid $neutral_200;
        border-radius: 0.6rem;
    }
    &__select {
        border-color: $primary_600;
    }
}
.select_loading {
    @include body_2();
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 4rem;
    color: $neutral_500;
    user-select: none;
}
/* <--------------------------- * SECTION1 * --------------------------- */
