/* <--------------------------- * SECTION1 * --------------------------- */
.dropDownV3_container {
    cursor: pointer;
    width: 11rem !important;
    .dropDownListV2_container {
        border-color: transparent;
        padding-left: 0.5rem;
        &:focus {
            background: $neutral_50;
            border-radius: 0.6rem;
        }
        .dropDownListV2_content {
            @include body_2();
            color: $neutral_500;
        }
        svg {
            margin-right: 1rem;
            font-size: 1.1rem;
            order: -1;
        }
    }
}
.dropDownV3_floatingContainer {
    .kite_body {
        width: 11.8rem;
        height: 8.4rem;
    }
    .dropDownListV2_labelContainer {
        padding: 0.4rem;
    }
    .dropDownListV2_labelContent {
        @include body_2();
        height: 2.8rem;
        line-height: 2.8rem !important;
        padding-left: 0.8rem;
        border-radius: 0.6rem;
    }
    .dropDownListV2_floatingLabel,
    .dropDownListV2_floatingLabel:hover,
    .dropDownListV2_floatingLabelLast {
        &:hover {
            background-color: transparent;
            .dropDownListV2_labelContent {
                background: $primary_50;
            }
        }
    }
    .dropDownListV2_floatingLabelActive {
        .dropDownListV2_labelContent {
            background: $primary_100 !important;
            color: $primary_600;
        }
    }
    .dropDownListV2_floatingWrap__large {
        box-shadow: $shadow_regular;
        border-radius: 0.6rem;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
