.builtInScriptInstruction_Container {
    font: $fs_r_14;
    color: $type;
    > div:last-child {
        height: 50rem;
    }
}

.builtInScriptInstruction_title {
    display: flex;
    justify-content: space-between;
    width: 100%;
    position: relative;
    border-bottom: 0.1rem solid $neutral_200;
    font: $fs_r_12;
    .builtInScriptInstruction_scriptIcon {
        position: relative;
        display: inline-block;
        width: 5.2rem;
        height: 3.7rem;
        line-height: 3.7rem;
        text-align: center;
        border-right: 1px solid #ebebeb;
    }
    .builtInScriptInstruction_scriptName {
        display: flex;
        align-items: center;
        flex: 1;
        padding-left: 1rem;
        > input {
            display: inline-block;
            line-height: 3.7rem;
            height: 3.7rem;
            text-align: left;
            width: 10.8rem;
            padding-left: 1rem;
            padding-right: 1rem;
        }
        .builtInScriptInstruction_label {
            @include label_2();
            font-size: 1.2rem;
            line-height: 3.7rem;
            margin-left: 3rem;
            padding: 0 0.4rem;
            color: $auxiliary_600;
            line-height: 1.4rem;
            text-align: center;
            border: 0.1rem solid $auxiliary_600;
            border-radius: 0.4rem;
            cursor: pointer;
            > span {
                transform: scale(0.833);
                margin-right: 0;
            }
        }
    }
    .builtInScriptInstruction_updateButton {
        @include label();
        position: absolute;
        right: 1.1rem;
        width: 5.6rem;
        height: 2.4rem;
        line-height: 2.4rem;
        text-align: center;
        color: $primary_600;
        border: 0.1rem solid $primary_600;
        border-radius: 0.6rem;
        cursor: pointer;
        .builtInScriptInstruction_dot {
            position: absolute;
            top: -0.4rem;
            right: -0.4rem;
            width: 0.8rem;
            height: 0.8rem;
            background: $danger_600;
            border-radius: 50%;
        }
    }
}
.builtInScriptInstruction_tag {
    @include subtitle_2();
    height: 4rem;
    padding-left: 1.6rem;
    line-height: 4rem;
    color: $neutral_600;
    border-bottom: 1px dashed #ebebeb;
    &:first-child {
        margin-top: 0rem;
    }
    span {
        display: inline-block;
        padding-right: 1.6rem;
        border-right: 1px solid $neutral_200;
    }
}
.builtInScriptInstruction_content {
    padding-left: 1.5rem;
    height: 12.8rem;
    @include body_2();
    border-bottom: 1px solid $neutral_200;
    .textEditor_wrapper {
        padding-left: 1.6rem;
    }
    li {
        display: flex;
        align-items: center;
        list-style: none;
        @include body_2();
        width: 100%;
        margin-bottom: 0.4rem;
        padding: 0.6rem 1.6rem;
        height: 3rem;
        transition: all 0.5s ease;
        box-sizing: border-box;
        &::before {
            display: inline-block;
            content: '';
            width: 0.6rem;
            height: 0.6rem;
            background: $neutral_500;
            border-radius: 50%;
        }
        > span {
            display: inline-block;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            &:nth-of-type(1) {
                padding-left: 0.8rem;
                width: 16%;
            }
            &:nth-of-type(2) {
                width: 32%;
            }
            &:nth-of-type(3) {
                width: 51%;
            }
        }
        .builtInScriptInstruction_more {
            display: none;
        }
        svg {
            color: $primary_600;
            cursor: pointer;
        }
    }
}
.builtInScriptInstruction_return {
    display: flex;
    flex-direction: column;
    padding-left: 1.6rem;
    padding-top: 0.8rem;
    > span {
        &:first-child {
            @include subtitle_2();
            height: 2.4rem;
            line-height: 2.4rem;
            color: $neutral_500;
        }
        &:last-child {
            @include body_2();
            color: $neutral_400;
        }
    }
}
