/* <--------------------------- * SECTION1 * --------------------------- */
.addNodeModal_container {
    position: relative;
    padding: 3.2rem 0;
    width: 34.7rem;
    height: 41.5rem;
    box-sizing: border-box;
}
.addNodeModal_nodeName {
    display: flex;
    align-items: center;
    padding: 0 1.9rem;
    > span {
        @include subtitle_3();
        margin-right: 1.3rem;
        color: $neutral_500;
    }
    > input {
        flex: 1;
    }
}
.addNodeModal_list {
    width: 30.9rem;
    height: 26.8rem;
    margin-left: 1.9rem;
    border: 0.6px solid $line_light;
    border-radius: 0.4rem;
    overflow-y: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
        width: 0 !important;
    }
}
.addNodeModal_errorMsg {
    margin-left: 9rem;
    height: 1.6rem;
    font: $fs_r_10;
    color: $red;
}
.addNodeModal_item {
    display: flex;
    align-items: center;
    min-height: 4.5rem;
    padding-left: 1rem;
    font-size: 0;
    cursor: pointer;
    > span {
        @include body_2();
        color: $neutral_500;
    }
    &_arrow {
        display: flex;
        align-items: center;
        margin-right: 1.2rem;
        width: 1rem;
        > svg {
            font-size: 1rem;
        }
    }
    &_nodeIcon {
        margin-right: 1rem;
        width: 1.5rem;
        > svg {
            font-size: 1.6rem;
            color: $primary_600;
        }
    }
    &_nodeContent {
        margin-right: 1rem;
    }
    &__select {
        background: rgba($primary_variant, 0.15) !important;
    }
    &:hover {
        background: rgba(71, 141, 165, 0.06);
    }
    &_tip {
        svg {
            font-size: 1.4rem;
        }
    }
}
.addNodeModal_item_child {
    display: flex;
    align-items: center;
    min-height: 3rem;
    padding-left: 5rem;
    font-size: 0;
    cursor: pointer;
    &__select {
        background: rgba($primary_variant, 0.15) !important;
    }
    > span {
        @include body_2();
        color: $neutral_500;
    }
}
.addNodeModal_buttons {
    display: flex;
    margin-top: 2.6rem;
    border-top: 0.1rem solid $neutral_300;
    > div {
        display: inline-block;
        width: 50%;
        height: 3.6rem;
        line-height: 3.6rem;
        text-align: center;
        cursor: pointer;
        &:nth-of-type(1) {
            border-right: 0.1rem solid $neutral_300;
        }
        &:nth-of-type(2) {
            color: $primary_600;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
