/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@import '~/Constant/base.scss';
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/
@mixin demo {
}
$shadow_large: 0px 16px 24px -2px rgba(26, 26, 26, 0.12), 0px 2px 12px rgba(26, 26, 26, 0.1);
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.questionOptionItem_container {
    background: #aaa;
}
.questionOptionItem_container_drag {
    opacity: 0.3;
}
.questionOptionItem_container_hover {
    box-shadow: $shadow_large;
    .questionOptionItem_questionList_item {
        border: none !important;
        &::after {
            height: 0 !important;
        }
    }
    * {
        cursor: grab !important;
    }
}
.questionOptionItem_buttonIcon {
    display: flex;
    align-items: center;
    height: 100%;
    cursor: pointer;
}
.questionOptionItem_drag_icon {
    cursor: grab !important;
    display: flex;
    align-items: center;
    height: 100%;
    > svg {
        cursor: grab !important;
    }
}
.questionOptionItem_questionList_item {
    position: relative;
    display: flex;
    width: 100%;
    min-height: 4rem;
    border-top: 0.1rem solid $line_light;
    // border-bottom: 0.1rem solid $line_light;
    // margin-top: -1px;
    background: $background;
    &::after {
        content: '';
        position: absolute;
        bottom: -0.1rem;
        width: 100%;
        height: 0.1rem;
        background: $line_light;
    }
}
.questionOptionItem_buttonContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 7.5rem;
    min-height: 4rem;
    padding: 0 1rem;
    font-size: 1.6rem;
    color: $line_light;
    border-right: 0.1rem solid $line_light;
    box-sizing: border-box;
    svg {
        cursor: pointer;
    }
}
.questionOptionItem_code {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 100%;
    border-right: 0.1rem solid $line_light;
    input {
        width: 4.9rem;
        height: 100%;
        text-align: center;
        border: none;
        outline: none;
    }
}
.questionOptionItem_contentContainer {
    flex: 1;
    display: flex;
    align-items: center;
    span {
        white-space: wrap !important;
    }
    .scroll_scrollBody {
        padding-left: 1.6rem !important;
    }
    &:hover {
        .questionOptionItem_optionSetting {
            opacity: 1;
        }
    }
}
.questionOptionItem_optionSetting {
    cursor: pointer;
    svg {
        color: $primary_600;
        cursor: pointer;
    }
}
.questionOptionItem_optionFixPosition {
    padding-right: 2.2rem;
    svg {
        font-size: 1.6rem;
        color: #9197a1;
        cursor: pointer;
    }
}
.blockNode_list_nodesAdd {
    height: 4rem;
    display: flex;
    align-items: center;
    border-top: 0.1rem solid $line_light;
    color: $line_light;
    cursor: pointer;
    > span {
        &:nth-of-type(1) {
            font-size: 1.8rem;
            color: $primary_600;
            margin-left: 1.3rem;
            margin-right: 0.8rem;
        }
        &:nth-of-type(2) {
            font: $fs_r_12;
        }
    }
}
.questionOptionItem_before {
    &::after {
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: $primary_600;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
