/* <--------------------------- * SECTION1 * --------------------------- */
.multiMatrix_table {
    width: 100%;
    table-layout: auto;
    border-collapse: collapse;
    border-spacing: 0;
    font-size: $fs_r_14;
    word-break: break-all;
    margin: 0 1rem;
    font: $fs_r_14;
    td {
        position: relative;
        border: 0.1rem solid #e1e7f2;
    }
}
.multiMatrix_head {
    th {
        text-align: center;
        padding: 1.6rem 0;
        font-weight: normal;
        min-width: 8rem;
        border: 0.1rem solid #e1e7f2;
    }
}
.multiMatrix_title {
    width: 20%;
    min-width: 10rem;
    max-width: 17.5rem;
    padding: 0.8rem;
    position: relative;
}
.multiMatrix_option {
    display: inline-flex;
    width: 100%;
    min-width: 4.8rem;
    height: 4.8rem;
    justify-content: center;
    .check_icon {
        margin-right: 0;
        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }
    .check_wrap {
        width: 4.8rem;
        height: 4.8rem;
        justify-content: center;
        cursor: pointer;
        &:hover {
            background: $primary_50;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
