/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@import '~/Constant/base.scss';
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/
@mixin demo {
}

@mixin name {
    position: absolute;
    font-size: 1rem;
    top: 0rem;
    transition: 0.3s;
}
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.questionNode_switchContainer {
    position: relative;
    display: inline-block;
    vertical-align: top;
    width: 12.5rem;
    height: 8rem;
    padding-left: 1.3rem;
}

.questionNode_switchInput {
    height: 0;
    width: 0;
    visibility: hidden;
}

.questionNode_switchInput:checked + .questionNode_switchLabel::after {
    left: 2rem;
}

.questionNode_switchInput:checked + .questionNode_switchLabel {
    background: rgba($color: $primary, $alpha: 0.5);
    border: 0.1rem solid $background;
    transition: 0.3s;
}

.questionNode_switchLabel {
    cursor: pointer;
    width: 3.8rem;
    height: 1.4rem;
    border-radius: 1.4rem;
    top: 3.9rem;
    left: 5rem;
    position: absolute;
    display: inline-block;
    border: 0.1rem solid $line_light;
    box-sizing: border-box;
    transition: 0.3s;
}

.questionNode_switchLabel::after {
    content: '';
    position: absolute;
    height: 1.8rem;
    width: 1.8rem;
    left: -0.1rem;
    top: -0.3rem;
    border-radius: 1.8rem;
    background: $primary_600;
    box-shadow: $shadow_01dp;
    transition: 0.3s;
}
.questionNode_switchName1 {
    @include name();
    left: -1.8rem;
    color: $line_light;
}

.questionNode_switchName1__selected {
    @include name();
    left: -3.8rem;
    color: $type;
}

.questionNode_switchName2 {
    @include name();
    right: -1.8rem;
    color: $line_light;
}

.questionNode_switchName2__selected {
    @include name();
    right: -2.8rem;
    color: $type;
}

/* <--------------------------- * SECTION1 * --------------------------- */
