.appearanceTesting_container {
    padding: 1.6rem 2.4rem;
    min-height: 63rem;
    box-sizing: border-box;
}
.appearanceTesting_title {
    display: flex;
    justify-content: space-between;
    &_name {
        @include subtitle_2();
        white-space: nowrap;
    }
    &_addButton {
        display: inline-flex;
        align-items: center;
        font-size: 1.4rem;
        color: $primary_600;
        cursor: pointer;
        svg {
            margin-right: 0.7rem;
        }
    }
}

.appearanceTesting_paperList {
    margin-top: 2.5rem;
}
.appearanceTesting_paperItem {
    width: 100%;
    padding: 1.6rem;
    border: 0.1rem solid $neutral_200;
    border-radius: 1rem;
    box-sizing: border-box;
    &:nth-of-type(n + 2) {
        margin-top: 2rem;
    }
}
.appearanceTesting_paperName {
    display: flex;
    align-items: center;
    > span {
        @include body_2();
        width: 14.3rem;
        color: $neutral_600;
    }
    > div {
        flex: 1;
        display: flex;
        justify-content: space-between;
        @include body_2();
        color: $neutral_400;
        svg {
            font-size: 1.4rem;
            color: $primary_600;
            cursor: pointer;
        }
    }
}
.appearanceTesting_paperApplied {
    display: flex;
    align-items: center;
    > span {
        &:nth-of-type(1) {
            @include body_2();
            width: 14.3rem;
            color: $neutral_600;
        }
    }
    > div {
        @include body_2();
        display: flex;
        color: $neutral_400;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
