.appearancePreview_container {
    min-height: 63rem;
    box-sizing: border-box;
}
.appearancePreview_header {
    margin: 2rem 1.2rem;
    &_title {
        @include subtitle_2();
        font-size: 1.4rem;
        font-weight: 500;
        margin-left: 1rem;
        color: $neutral_400;
    }
}
.appearancePreview_question {
    width: 100%;
    margin-top: 3rem;
    padding: 1rem 1.4rem;
    box-sizing: border-box;
    &_title {
        @include h_3();
        margin-left: 1rem;
        margin-bottom: 2rem;
    }
    .open_item {
        margin-left: 0.5rem;
    }
    .numberInput_container {
        margin-left: 0.5rem;
    }
}
