.footer_container {
    position: fixed;
    background: $primary_600;
    z-index: 18;
    height: 3.6rem;
    width: 100vw;
    bottom: 0;
    color: $background;
    font: $fs_r_12;
    display: flex;
    align-items: center;
    user-select: none;
    justify-content: space-between;

    > div {
        display: flex;
        align-items: center;
    }

    > div > div {
        display: inline-flex;
        height: 2.7rem;
        align-items: center;
        padding-left: 0.5rem;
        padding-right: 0.5rem;
        border-radius: 0.6rem;
        position: relative;
        border: 0.06rem dashed transparent;

        &:hover {
            border: 0.06rem dashed rgba($background, 1);

            .footer_Hint {
                display: block;
            }
        }
    }
}

.footer_name {
    margin-left: 2.5rem;
    min-width: 14rem;
    text-align: center;
    justify-content: center;
    cursor: pointer;
}

.footer_modify {
    margin-left: 1.5rem;
    text-align: center;
    cursor: pointer;
}

.footer_version {
    margin-left: 0.7rem;
    text-align: center;
    cursor: pointer;
    > div {
        height: 1.8rem;
        line-height: 1.8rem;
        padding: 0 1rem;
        border: 0.1rem solid $background;
        border-radius: 1.4rem;
    }
}

.footer_Saving {
    cursor: pointer;
    text-align: center;

    > span {
        margin-left: 0.7rem;
    }
}

.footer_Lan {
    margin-right: 3rem;
    margin-left: 2rem;
    text-align: center;
    cursor: pointer;
    position: relative;

    .footer_LanSelection {
        position: absolute;
        background: rgba($background, 1);
        height: 6.8rem;
        top: -7.5rem;
        box-shadow: $shadow_06dp;
        border-radius: 0.4rem;
        left: 0;
        padding-top: 1rem;

        > div {
            color: $gray_2;
            font: $fs_r_12;
            height: 2rem;
            margin-top: 0.8rem;
            display: flex;
            align-items: center;
            padding: 0 1rem;

            &:hover {
                background: rgba($primary_600, 0.2);
            }
        }

        > div:first-child {
            margin-top: 0;
        }
    }

    > span {
        display: flex;
        align-items: center;
        justify-content: center;
        font: $fs_r_10;
        margin-right: 0.5rem;
        height: 1.4rem;
        width: 3.4rem;
        padding: 0 2rem;
        background: rgba($background, 0.2);
        border: 0.08rem solid rgba($background, 1);
        border-radius: 0.2rem;
    }

    .footer_Hint {
        top: -7rem;
    }
}

.footer_Hint {
    position: absolute;
    display: none;
    top: -5rem;
    left: 50%;
    transform: translateX(-50%);
}

.footer_Saving_selected {
    background: rgba($background, 0.2);

    &:hover {
        .footer_Hint {
            display: none !important;
            border: 0.06rem dashed transparent !important;
        }
    }
}

