/* <--------------------------- * SECTION1 * --------------------------- */
.fontBackgroundButton_list {
    margin-top: 0.8rem;
    padding: 1.2rem 0.4rem;
    width: 12rem;
    height: 34rem;
    background: #ffffff;
    box-shadow: 0px 2px 8px rgba(26, 26, 26, 0.08), 0px 2px 4px rgba(26, 26, 26, 0.03);
    border-radius: 6px;
    transform: translateX(-1rem);
    .fontBackgroundButton_item {
        &:nth-of-type(n + 2) {
            margin-top: 0.8rem;
        }
    }
}
.fontBackgroundButton_item {
    display: flex;
    align-items: center;
    padding: 0 0.8rem;
    height: 2.8rem;
    border-radius: 0.4rem;
    cursor: pointer;
    span {
        @include body_2();
    }
    svg {
        margin-right: 1.2rem;
        font-size: 1.8rem;
    }
    &:hover {
        background: $primary_10;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
