/* <--------------------------- * SECTION1 * --------------------------- */
.blockOptionItem_itemContainer {
    display: flex;
    border-bottom: 0.1rem solid $line_light;
}
.blockOptionItem_dragging {
    background: white;
    box-shadow: $shadow_large;
    border: none !important;
}
.blockOptionItem_isDragging {
    opacity: 0.3;
}
.blockOptionItem_buttonContainer {
    display: flex;
    align-items: center;
    width: 4.9rem;
    height: 4rem;
    line-height: 4rem;
    color: $line_light;
    border-right: 0.1rem solid $line_light;
    svg {
        font-size: 1.4rem;
    }
}
.blockOptionItem_button {
    display: inline-block;
    cursor: pointer;
    margin-left: 0.8rem;
}

.blockOptionItem_button {
    display: inline-block;
    margin-left: 0.8rem;
    cursor: pointer;
}
.blockOptionItem_contentContainer {
    display: inline-block;
    width: calc(100% - 4.9rem);
    vertical-align: top;
    height: 4rem;
    line-height: 4rem;
    position: relative;
    padding-left: 1.3rem;
}

.blockOptionItem_contentContainer span {
    display: inline-block;
    width: calc(100% - 14.9rem);
    height: 4rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.blockOptionItem_jumpButton {
    font: $fs_m_14;
    color: $background;
    user-select: none;
    text-align: center;
    position: absolute;
    right: 2.1rem;
    top: 0.7rem;
    width: 11.8rem;
    height: 2.6rem;
    line-height: 2.6rem;
    background: $primary_600;
    border-radius: 0.4rem;
    cursor: pointer;
    transition: all 0.3s ease;
}
/* <--------------------------- * SECTION1 * --------------------------- */
