/* <--------------------------- * SECTION1 * --------------------------- */
.toolButton_toolItem {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 2.2rem;
    height: 2.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    &:nth-of-type(n + 2) {
        margin-left: 0.8rem;
    }
    &:hover {
        background: $primary_50;
    }
    svg {
        font-size: 1.6rem;
        color: $neutral_400;
    }
}
.toolButton_toolItem__active {
    svg {
        color: $primary_600 !important;
    }
}

.toolBar_tip {
    .kite_body {
        display: flex;
        align-items: center;
        width: auto;
        height: 3.2rem;
        font-size: 1.2rem;
        white-space: nowrap;
        > span {
            line-height: 1.6rem;
        }
        svg {
            margin-right: 0.4rem;
            font-size: 1.6rem;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
