/* <--------------------------- * SECTION1 * --------------------------- */
.sash_container {
    position: relative;
}
.sash_line {
    position: absolute;
    top: 0;
    right: -0.3rem;
    width: 0.6rem;
    height: 100%;
    background: transparent;
    z-index: 2;
    transition: all 0.2s ease;
}
.sash_line_hover {
    &:hover {
        background: $neutral_300;
        cursor: col-resize;
    }
}
.sash_line_left {
    right: -0.25rem;
}
.sash_line_right {
    left: -0.25rem;
}
.sash_line_drag {
    background: $neutral_300;
}
/* <--------------------------- * SECTION1 * --------------------------- */
