.toolBar_container {
    position: absolute;
    display: flex;
    align-items: center;
    transform: translateY(-100%);
    width: 59rem;
    height: 3rem;
    background: $background;
    border: 0.1rem solid $neutral_200;
    // box-shadow: $shadow_regular;
    border-radius: 2px;
    opacity: 1;
    z-index: 999;
}
.toolBar_comment {
    display: flex;
    align-items: center;
    padding-left: 0.9rem;
    width: 10rem;
    height: 100%;
    border-right: 0.1rem solid #e0e0e0;
    cursor: pointer;
    user-select: none;
    svg {
        margin-right: 0.4rem;
        font-size: 1.4rem;
        color: $neutral_500;
    }
    > span {
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 14px;
        color: $neutral_500;
    }
}
.toolBar_font {
    display: flex;
    align-items: center;
    padding: 0 0.8rem;
    height: 100%;
    border-right: 0.1rem solid #e0e0e0;
    span {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 2.2rem;
        height: 2.2rem;
        border-radius: 0.4rem;
        cursor: pointer;
        &:nth-of-type(n + 2) {
            margin-left: 0.8rem;
        }
        &:hover {
            background: $primary_50;
        }
    }
    svg {
        font-size: 1.6rem;
        color: $neutral_400;
    }
}
