/* <--------------------------- * SECTION1 * --------------------------- */
.command_container {
    position: absolute;
    padding: 0.8rem 0;
    width: 23.7rem;
    height: 14.8rem;
    box-sizing: border-box;
    // box-shadow: $shadow_regular;
    background: $background;
    border-radius: 0.6rem;
    user-select: none;
    opacity: 1;
    transition: opacity 0.75s;
    z-index: 9;
}
.command_title {
    @include subtitle_3();
    margin-left: 0.8rem;
}
.command_list {
    margin-top: 0.4rem;
}
.command_itemContainer {
    padding: 0 0.8rem;
    &:hover {
        .command_item {
            background: $primary_10;
        }
        .command_item_script {
            display: block;
        }
    }
}
.command_item {
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 0.8rem;
    height: 4.8rem;
    border-radius: 0.6rem;
    cursor: pointer;
    &_icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 2.4rem;
        height: 2.4rem;
        font-size: 1.3rem;
        color: $primary_600;
        background: $primary_10;
        border-radius: 0.4rem;
    }
    &_detail {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 4rem;
        margin-left: 1.1rem;
        .command_detail_title {
            @include body_2();
            color: $neutral_500;
        }
        .command_detail_description {
            @include label();
            color: $neutral_300;
        }
    }
    &_arrow {
        position: absolute;
        top: 50%;
        right: 1rem;
        transform: translateY(-50%);
        color: $neutral_400;
        > svg {
            font-size: 1.4rem;
        }
    }
    &:nth-of-type(n + 2) {
        margin-top: 0.8rem;
        &::after {
            content: '';
            position: absolute;
            top: -0.6rem;
            left: 0;
            width: 20.5rem;
            height: 0.1rem;
            background: $neutral_200;
            z-index: 2;
        }
    }
}
.command_item_script {
    display: none;
    position: absolute;
    left: 23rem;
    top: 0rem;
}
.command_item_content {
    padding: 0.8rem;
    width: 16.7rem;
    height: 8rem;
    box-sizing: border-box;
    background: $background;
    // box-shadow: $shadow_regular;
    border-radius: 0.6rem;
    > div {
        @include body_2();
        display: flex;
        align-items: center;
        padding-left: 0.8rem;
        height: 2.8rem;
        border-radius: 0.6rem;
        color: $neutral_500;
        &:nth-of-type(n + 2) {
            margin-top: 0.8rem;
        }
        &:hover {
            background: $primary_10;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
