/* <--------------------------- * SECTION1 * --------------------------- */
.toolTip_tip {
    .kite_body {
        padding: 0;
        padding-right: 0.4rem;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: auto;
        font-size: 1.2rem;
        user-select: none;
        height: auto;
        > span {
            line-height: 1.6rem;
            user-select: none;
        }
        svg {
            margin-right: 0.4rem;
            font-size: 1.6rem;
            user-select: none;
        }
    }
}
.toolTip_scroll {
    .scroll_scrollBar__vertical {
        width: 0.2rem;
        background-color: $neutral_400;
    }
}
.toolTip_scrollBody {
    overflow-wrap: break-word;
    padding-right: 0.6rem;
}
