/* <--------------------------- * SECTION1 * --------------------------- */
.groupItem_container {
    position: relative;
    width: 50.6rem;
    margin-bottom: 1.6rem;
    border: 0.1rem solid $neutral_200;
    border-radius: 0.2rem;
    user-select: none;
}
.groupItem_line {
    position: absolute;
    top: 0;
    left: -0.1rem;
    width: 0.2rem;
    height: 100%;
    background: $purple_600;
    border-radius: 0.2rem;
}
.groupItem_title {
    @include subtitle_3();
    display: flex;
    align-items: center;
    padding-left: 1.2rem;
    height: 3.2rem;
    line-height: 3.2rem;
    color: $neutral_500;
    background: rgba($purple_600, 0.1);
    .groupItem_move {
        margin-right: 1.25rem;
        color: $neutral_300;
        cursor: pointer;
    }
    .groupItem_setting {
        display: none;
        align-items: center;
        margin-right: 0.8rem;
        color: $primary_600;
        cursor: pointer;
        &__select {
            display: flex;
        }
        &__disable {
            display: none !important;
        }
    }
    .groupItem_unfold {
        flex: 1;
        margin-right: 1rem;
        text-align: right;
        font-size: 1.2rem;
        cursor: pointer;
    }
    &:hover {
        .groupItem_setting {
            display: flex;
        }
    }
    .groupItem_icon_random {
        margin-right: 1rem;
    }
}
.groupItem_setting_groupName {
    background: none;
    border: none;
    outline: none;
}
.groupItem_item {
    @include body_2();
    display: flex;
    align-items: center;
    padding-left: 1.2rem;
    height: 2.8rem;
    color: $purple_600;
    .groupItem_move {
        margin-right: 0.9rem;
        color: $neutral_300;
        cursor: pointer;
    }
    &:hover {
        box-shadow: $shadow_small;
        .groupItem_delete {
            display: block;
        }
    }
}
.groupItem_item__disable {
    color: $neutral_300 !important;
    .groupItem_move {
        visibility: hidden;
    }
    &:hover {
        box-shadow: none !important;
        .groupItem_delete {
            display: none !important;
        }
    }
}
.groupItem_content {
    flex: 1;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.groupItem_delete {
    display: none;
    padding-left: 1rem;
    padding-right: 1.6rem;
    font-size: 1.6rem;
    color: $danger_600;
    text-align: right;
    svg {
        cursor: pointer;
    }
}
.groupItem_dragArea {
    width: 100%;
    height: 2.8rem;
    border: 0.2rem dashed #ccc;
    border-radius: 0.2rem;
}
.moveGroupItem {
    position: fixed;
    height: 3.2rem;
    background: $background;
    z-index: 100;
    box-shadow: $shadow_small;
    cursor: pointer;
    border-radius: 0.4rem;
    pointer-events: none;
    overflow: hidden;
}
/* <--------------------------- * SECTION1 * --------------------------- */
