/* <--------------------------- * SECTION1 * --------------------------- */
.turnIntoButton_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 6.8rem;
    font-size: 1.2rem;
    cursor: pointer;
    svg {
        transform: translateY(-0.3rem);
    }
}
.turnIntoButton_list {
    margin-top: 0.8rem;
    padding: 0.8rem 0.4rem;
    width: 12rem;
    height: 8rem;
    background: #ffffff;
    box-shadow: 0px 2px 8px rgba(26, 26, 26, 0.08), 0px 2px 4px rgba(26, 26, 26, 0.03);
    border-radius: 6px;
    transform: translateX(-1rem);
    .turnIntoButton_item {
        &:nth-of-type(n + 2) {
            margin-top: 0.8rem;
        }
    }
}
.turnIntoButton_item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.8rem;
    height: 2.8rem;
    border-radius: 0.4rem;
    cursor: pointer;
    span {
        @include body_2();
    }
    &:hover {
        background: $primary_10;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
