/* <--------------------------- * SECTION1 * --------------------------- */
.scriptTree_item {
    display: flex;
    align-items: center;
    padding-left: 3.2rem;
    height: 4rem;
    font: $fs_r_14;
    color: #4d4d4d;
    cursor: pointer;
    svg {
        margin-right: 1rem;
        font-size: 1.6rem;
        color: #22a6b3;
    }
    &:hover {
        background: rgba($primary_variant, 0.07);
        transition: 0.2s;
    }
    &__select {
        background: rgba($primary_variant, 0.07);
    }
}
.scriptTree_name {
    position: relative;
    width: calc(100% - 0rem);
    span {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
}

.ScriptTree_label_draft {
    @include label_2();
    font-size: 1.2rem;
    position: absolute;
    top: 50%;
    right: 1.4rem;
    height: 1.4rem;
    padding: 0 0.4rem;
    transform: translateY(-50%);
    color: $yellow_600;
    line-height: 1.4rem;
    text-align: center;
    border: 0.1rem solid $yellow_600;
    border-radius: 0.4rem;
    z-index: 2;
    > span {
        transform: scale(0.833);
        margin-right: 0;
    }
}
.ScriptTree_label_verified {
    @include label_2();
    font-size: 1.2rem;
    position: absolute;
    top: 50%;
    right: 1.4rem;
    height: 1.4rem;
    padding: 0 0.4rem;
    transform: translateY(-50%);
    color: $success_600;
    line-height: 1.4rem;
    text-align: center;
    border: 0.1rem solid $success_600;
    border-radius: 0.4rem;
    z-index: 2;
    > span {
        transform: scale(0.833);
        margin-right: 0;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
