/* <--------------------------- * SECTION1 * --------------------------- */
.switchType_container {
    display: inline-block;
    width: 7.2rem;
    height: 3.7rem;
    line-height: 3.7rem;
    text-align: center;
    color: $primary_600;
    position: relative;
    cursor: pointer;
    > svg {
        &:nth-of-type(2) {
            margin-left: 0.8rem;
            transition: all 0.2s ease;
            margin-top: 0.2rem;
            font-size: 1.2rem;
            color: $neutral_500;
        }
    }
}
.switchType_KindSelectionList {
    position: absolute;
    width: 12.5rem;
    box-shadow: $shadow_04dp;
    background: $background;
    z-index: 9;
    text-align: left;
    color: $type;
    padding: 1.2rem 0.4rem;
    border-radius: 0.4rem;
    color: $neutral_500;
    user-select: none;
    svg {
        color: $neutral_400;
    }
    > div {
        @include body_2();
        height: 2.8rem;
        line-height: 2.8rem;
        border-radius: 0.4rem;
        &:hover {
            background: $primary_50;
        }
        > div {
            color: $primary_600;
            display: inline-block;
            width: 4.6rem;
            padding-left: 1.42rem;
        }
        &:nth-of-type(n + 2) {
            margin-top: 0.8rem;
        }
    }
    .switchType_select {
        background: $primary_10 !important;
        color: $primary_600;
        svg {
            color: $primary_600;
        }
    }
    &::after {
        position: absolute;
        top: -0.6rem;
        left: 0.8rem;
        content: '';
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 1rem 1rem 1rem;
        border-color: transparent transparent $background transparent;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
