/* <--------------------------- * SECTION1 * --------------------------- */
.quotaOptionItem_nodesItem {
    cursor: pointer;
}
.quotaOptionItem_nodesContainer {
    height: 4rem;
    white-space: nowrap;
    border-bottom: 0.1rem solid $line_light;
}
.quotaOptionItem_buttonContainer {
    display: inline-flex;
    justify-content: space-between;
    width: 7.5rem;
    height: 4rem;
    line-height: 4rem;
    color: $line_light;
    border-right: 0.1rem solid $line_light;
    padding: 0 1rem;
    box-sizing: border-box;
    svg {
        font-size: 1.6rem;
    }
    .quotaOptionItem_button {
        display: inline-block;
        cursor: pointer;
    }
}
.quotaOptionItem_dragging {
    background: white;
    box-shadow: $shadow_large;
    border: none !important;
}
.quotaOptionItem_isDragging {
    opacity: 0.3;
}
.quotaOptionItem_contentContainer {
    display: inline-block;
    vertical-align: top;
    width: calc(100% - 9rem);
    height: 4rem;
    line-height: 4rem;
    position: relative;
    padding-left: 1.3rem;
    > span {
        display: none;
        color: $primary_600;
        margin-right: 1.3rem;
        cursor: pointer;
    }
    > input {
        height: 3.9rem;
        vertical-align: top;
        width: calc(100% - 20rem);
        border: none;
        outline: none;
    }
    &:hover {
        > span {
            display: inline;
        }
        > input {
            width: calc(100% - 22.7rem);
        }
    }
}

.quotaOptionItem_jumpButton {
    user-select: none;
    text-align: center;
    position: absolute;
    right: 2.1rem;
    top: 0.7rem;
    height: 2.6rem;
    font: $fs_m_14;
    color: $primary_600;
    text-decoration: underline;
    line-height: 2.6rem;
    border-radius: 0.4rem;
    cursor: pointer;
    > span:first-child {
        position: absolute;
        left: -6.5rem;
        color: $gray_4;
        font-size: 2.3rem;
        > span {
            font: $fs_r_10;
            color: $type;
            position: absolute;
            bottom: 0.2rem;
            right: -1rem;
            background: $line_light;
            height: 1rem;
            line-height: 1rem;
        }
    }
}
.quotaOptionItem_jumpButton_tip {
    .kite_body {
        height: 9.2rem !important;
        width: 25.2rem !important;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
