/* <--------------------------- * SECTION1 * --------------------------- */
.questionNodeForm_title {
    &_title {
        height: 5rem;
        padding-top: 1.2rem;
        padding-left: 2.5rem;
        font: $fs_m_20;
        border-bottom: 0.1rem dashed $line_light;
        box-sizing: border-box;
    }
    &_content {
        @include h_3();
        min-height: 10rem;
        color: $neutral_600;
        padding: 1.2rem 2.5rem;
        .optionItemHtml_container {
            @include h_3();
        }
    }
}

.questionNodeForm_question {
    width: calc(100% - 0rem);
    border-top: 0.1rem solid $line_light;
    &_title {
        height: 5rem;
        padding-top: 1.2rem;
        padding-left: 2.5rem;
        font: $fs_m_20;
        border-bottom: 0.1rem dashed $line_light;
        box-sizing: border-box;
    }
    &_content {
        width: 100%;
        min-height: 15rem;
        font: $fs_r_12;
        padding: 1.2rem 1.5rem;
        box-sizing: border-box;
    }
}
.questionNodeForm_output {
    border-top: 0.1rem solid $line_light;
    color: $primary_600;
    &_title {
        display: flex;
        align-items: center;
        height: 5rem;
        padding-left: 2.5rem;
        font: $fs_m_20;
        border-bottom: 0.1rem dashed $line_light;
        box-sizing: border-box;
    }
    &_content {
        min-height: 15rem;
        padding: 1.2rem 2.5rem;
        font: $fs_r_12;
        box-sizing: border-box;
    }
    svg {
        margin-left: 1.6rem;
        color: $neutral_200;
    }
    &_tip {
        @include body_2();
        word-break: break-all;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
