/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@import '~/Constant/base.scss';
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/
@mixin demo {
}
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.customScriptInstruction_container {
    width: 100%;
    height: 50rem;
}
.ExecutionScript_Container {
    font: $fs_r_14;
    color: $type;
    > div:last-child {
        height: 50rem;
    }
}

.ExecutionScript_title {
    display: flex;
    justify-content: space-between;
    width: 100%;
    position: relative;
    border-bottom: 0.1rem solid $neutral_200;
    font: $fs_r_12;
    .ExecutionScript_scriptIcon {
        position: relative;
        display: inline-block;
        width: 5.2rem;
        height: 3.7rem;
        line-height: 3.7rem;
        text-align: center;
        border-right: 1px solid #ebebeb;
    }
    .ExecutionScript_scriptTitle {
        display: inline-block;
        height: 3.7rem;
        line-height: 3.7rem;
        width: 10.2rem;
        text-align: center;
        border-right: 1px solid #ebebeb;
    }
    .ExecutionScript_scriptName {
        display: flex;
        align-items: center;
        flex: 1;
        padding-left: 1rem;
        > input {
            display: inline-block;
            line-height: 3.7rem;
            height: 3.7rem;
            text-align: left;
            width: 10.8rem;
            padding-left: 1rem;
            padding-right: 1rem;
        }
        .ExecutionScript_label_draft {
            @include label_2();
            font-size: 1.2rem;
            line-height: 3.7rem;
            padding: 0 0.4rem;
            color: $yellow_600;
            line-height: 1.4rem;
            text-align: center;
            background: $yellow_50;
            border: 0.1rem solid $yellow_600;
            border-radius: 0.4rem;
            cursor: pointer;
            > span {
                transform: scale(0.833);
                margin-right: 0;
            }
        }
        .ExecutionScript_label_verified {
            @include label_2();
            font-size: 1.2rem;
            line-height: 3.7rem;
            padding: 0 0.4rem;
            color: $success_600;
            line-height: 1.4rem;
            text-align: center;
            background: $success_100;
            border: 0.1rem solid $success_600;
            border-radius: 0.4rem;
            cursor: pointer;
            > span {
                transform: scale(0.833);
                margin-right: 0;
            }
        }
    }

    .ExecutionScript_test {
        @include body_2();
        display: flex;
        align-items: center;
        margin-right: 1.2rem;
        color: $primary_600;
        cursor: pointer;
        svg {
            margin-right: 0.5rem;
            font-size: 1.6rem;
        }
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */

.executionScript_tag {
    @include subtitle_2();
    height: 4rem;
    padding-left: 1.6rem;
    line-height: 4rem;
    color: $neutral_600;
    border-bottom: 1px dashed #ebebeb;
    &:first-child {
        margin-top: 0rem;
    }
}
.executionScript_addArguments {
    float: right;
    margin-right: 1.6rem;
    color: $primary_600;
    cursor: pointer;
    svg {
        margin-right: 0.682rem;
    }
}
.executionScript_list {
    height: 12.8rem;
    @include body_2();
    border-bottom: 1px solid #ebebeb;
    box-sizing: border-box;
    .textEditor_wrapper {
        padding-left: 1.6rem;
    }
    li {
        display: flex;
        align-items: center;
        list-style: none;
        @include body_2();
        width: 100%;
        margin-bottom: 0.4rem;
        padding: 0.6rem 1.6rem;
        height: 3rem;
        transition: all 0.5s ease;
        box-sizing: border-box;
        &::before {
            display: inline-block;
            content: '';
            width: 0.6rem;
            height: 0.6rem;
            background: $neutral_500;
            border-radius: 50%;
        }
        > span {
            display: inline-block;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            &:nth-of-type(1) {
                padding-left: 0.8rem;
                width: 16%;
            }
            &:nth-of-type(2) {
                width: 32%;
                svg {
                    margin-right: 0;
                    color: $neutral_500;
                    order: 2;
                }
            }
            &:nth-of-type(3) {
                max-width: 51%;
            }
        }
        .executionScript_more {
            display: none;
        }
        .dropDownListV2_wrap {
            width: 12rem !important;
        }
        svg {
            color: $primary_600;
            cursor: pointer;
        }
    }
}
.executionScript_content {
    height: 12.8rem;
    border-bottom: 1px solid #ebebeb;
    overflow: hidden;
}

.executionScript_information {
    width: 100%;
    height: 100%;
    padding: 0.5rem 1.6rem;
    outline: none;
    border: none;
    resize: none;
    box-sizing: border-box;
}
.executionScript_chooseType {
    .kite_body {
        height: 16.4rem !important;
    }
}

.executionScript_item__hover {
    box-shadow: $shadow_small;
    .executionScript_more {
        display: inline-block !important;
        margin-left: 0.3rem !important;
    }
    > span {
        &:nth-of-type(3) {
            width: calc(51% - 1rem) !important;
        }
    }
}
.executionScript_tag {
    > span {
        display: inline-block;
        padding-right: 1.6rem;
        border-right: 1px solid $neutral_200;
    }
}
.executionScript_returnSwitch {
    display: inline-flex;
    align-items: center;
    margin-left: 1.6rem;
    > span {
        margin-right: 1.6rem;
        color: $neutral_400;
    }
}
.executionScript_return {
    display: flex;
    flex-direction: column;
    padding-left: 1.6rem;
    padding-top: 0.8rem;
    > span {
        &:first-child {
            @include subtitle_2();
            height: 2.4rem;
            line-height: 2.4rem;
            color: $neutral_500;
        }
        &:last-child {
            @include body_2();
            color: $neutral_400;
        }
    }
}
.executionScript_switchContent {
    margin-left: 1rem;
    margin-right: 1rem;
    font-size: 1.2rem;
    color: white;
}
.executionScript_instruction {
    display: inline-flex;
    position: relative;
    margin-right: 1.1rem;
    cursor: pointer;
    max-width: calc(51% - 2.1rem);
    span {
        flex: 1;
        overflow: hidden;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    &:hover {
        .executionScript_instructionTip {
            display: block;
        }
    }
    .executionScript_instructionTip {
        display: none;
        position: absolute;
        top: -1rem;
        right: 0;
        padding: 1.6rem;
        width: 32rem;
        height: 13.6rem;
        box-shadow: $shadow_medium;
        background: $background;
        border-radius: 1rem;
        z-index: 999;
        transform: translate(0, -100%);
        z-index: 5;
        .scroll_scrollBody {
            word-break: break-all;
        }
        &::before {
            content: '';
            position: absolute;
            right: 3rem;
            bottom: -1rem;
            border: 0;
            border-style: solid;
            border-width: 1.6rem 1.6rem 0 1.6rem;
            border-color: $background transparent transparent transparent;
        }
    }
}
.executionScript_instruction__placeholder {
    @include body_2();
    color: $neutral_300;
}
.executionScript_argumentTip {
    position: absolute;
    padding: 1.6rem;
    width: 32rem;
    height: 13.6rem;
    box-shadow: $shadow_medium;
    background: $background;
    border-radius: 1rem;
    z-index: 999;
    transform: translate(0rem, 6rem);
    .scroll_scrollBody {
        word-break: break-all;
    }
    &::before {
        content: '';
        position: absolute;
        right: 3rem;
        bottom: -1rem;
        border: 0;
        border-style: solid;
        border-width: 1.6rem 1.6rem 0 1.6rem;
        border-color: $background transparent transparent transparent;
    }
}
.executionScript_argumentItem {
    margin-bottom: 1.2rem;
    > div {
        &:first-child {
            @include body_2();
            height: 2rem;
            line-height: 2rem;
            color: $neutral_600;
        }
        &:last-child {
            @include body_2();
            margin-top: 0.4rem;
            height: 2rem;
            line-height: 2rem;
            color: $neutral_400;
        }
    }
}
.executionScript_argumentPup {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: 20;
}
.executionScript_delete {
    position: absolute;
    top: 1.6rem;
    right: 1.6rem;
    color: $danger_600;
    cursor: pointer;
    user-select: none;
    span {
        @include body_2;
        margin-left: 0.631rem;
    }
}
.ExecutionScript_toolTip {
    .errorAlert_content {
        background: rgba(#212121, 0.8);
    }
}
.executionScript_testContainer {
    height: 50rem;
    background: $primary_10;
}
.executionScript_header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1.2rem;
    height: 3.6rem;
    border-bottom: 0.1rem solid $neutral_300;
    .executionScript_title {
        @include subtitle_3();
        color: $neutral_600;
    }
    .executionScript_back {
        @include body_2();
        color: $neutral_500;
        cursor: pointer;
        svg {
            margin-right: 0.4rem;
            font-size: 1.6rem;
        }
    }
}
.ExecutionScript_container {
    display: inline-block;
    position: relative;
    margin-top: 2rem;
    padding: 0 1.2rem;
    width: 100%;
    height: 43.6rem;
    .scroll_scrollBar__vertical {
        margin-right: -0.9rem;
    }
}

.executionScript_button {
    display: flex;
    align-items: center;
    margin: auto;
    margin-top: 1.6rem;
    padding: 0 0.8rem;
    width: 15.2rem;
    height: 2.8rem;
    color: $primary_600;
    text-align: center;
    border: 0.1rem solid $primary_400;
    border-radius: 0.6rem;

    cursor: pointer;
    span {
        @include body_2();
        display: flex;
        align-items: center;
        white-space: nowrap;
        &:nth-of-type(1) {
            margin-right: 0.8rem;
        }
        &:nth-of-type(2) {
            line-height: 1.4rem;
        }
    }
    svg {
        font-size: 1.4rem;
    }
}

.executionScript_confirmButton {
    position: absolute;
    right: 0;
    bottom: 0;
    margin-right: 1.2rem;
    background: $success_600;
    box-shadow: $shadow_small;
}
/* <--------------------------- * SECTION1 * --------------------------- */
