/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@import '~/Constant/base.scss';
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/
@mixin demo {
}
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.appearanceIntroduction_container {
    min-height: 63rem;
}
.appearanceIntroduction_title {
    margin-top: 1.5rem;
    @include subtitle_3();
    color: $neutral_600;
}
.appearanceIntroduction_instruction {
    margin-top: 1.6rem;
    @include body_2();
    color: $neutral_400;
    > p {
        display: inline-block;
    }
    b {
        color: $neutral_700;
    }
}
.appearanceIntroduction_foldButton {
    display: inline-flex;
    align-items: center;
    margin-left: 1.8rem;
    color: $primary_600;
    cursor: pointer;
    svg {
        margin-left: 0.62rem;
        font-size: 0.96rem;
    }
}
.appearanceIntroduction_plugin {
    margin-top: 3.2rem;
    > h4 {
        @include h_4();
        margin-bottom: 1.6rem;
    }
    li {
        margin-bottom: 2rem;
        padding-left: 1.2rem;
        width: 100%;
        height: 10.8rem;
        border: 0.1rem solid $neutral_200;
        border-radius: 1rem;
    }
    div {
        @include body_2();
        margin-top: 1.2rem;
        span {
            display: inline-block;
            &:nth-of-type(1) {
                width: 14.3rem;
                color: $neutral_600;
            }
            &:nth-of-type(2) {
                color: $neutral_400;
            }
        }
    }
}
.appearanceIntroduction_scroll {
    padding: 0 2.4rem;
    .scroll_scrollBar__vertical {
        margin-right: 0.5rem;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
