/* <--------------------------- * SECTION1 * --------------------------- */
.groupSettingsModal_container {
    position: relative;
    padding: 3.2rem 2.4rem;
    width: 80rem;
    height: 51.6rem;
    user-select: none;
    box-sizing: border-box;
}
.groupSettingsModal_title {
    display: flex;
    align-items: center;
    span {
        @include h_3();
        margin-left: 1rem;
        color: $neutral_600;
    }
    svg {
        font-size: 2rem;
        color: $primary_600;
    }
}
.groupSettingsModal_optionsTitle {
    @include body_1();
    display: flex;
    justify-content: space-between;
    margin-top: 2.4rem;
    padding-left: 3.2rem;
    margin-right: 3.2rem;
    color: $neutral_600;
}
.groupSettingsModal_optionsTable {
    margin-top: 1.2rem;
    margin-left: 3.2rem;
    margin-right: 3.2rem;
    width: 68.8rem;
    height: 31.2rem;
    border: 0.1rem solid $neutral_200;
    border-radius: 0.6rem;
    overflow: hidden;
}
.groupSettingsModal_head {
    display: flex;
    background: $bg;
    border-bottom: 0.1rem solid $neutral_200;
    span {
        @include subtitle_2();
        height: 3.2rem;
        line-height: 3.2rem;
    }
    &_options {
        width: 15.33rem;
        text-align: center;
        border-right: 0.1rem solid $neutral_200;
    }
    &_groups {
        flex: 1;
        text-align: center;
    }
}
.groupSettingsModal_body {
    display: flex;
}
.groupSettingsModal_hover {
    cursor: pointer;
    overflow: hidden;
}
.groupSettingsModal_options {
    width: 15.33rem;
    height: 28rem;
    background: $bg;
    border-right: 0.1rem solid $neutral_200;
}
.groupSettingsModal_options {
    padding: 1.2rem;
    &_item {
        @include body_2();
        margin-bottom: 1.6rem;
    }
}
.groupSettingsModal_groups {
    flex: 1;
    padding-top: 0.8rem;
}
.groupSettingsModal_scroll {
    padding: 0 1.35rem;
}
.groupSettingsModal_options_item {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.groupSettingsModal_defaultContainer {
    position: relative;
    margin-bottom: 1.6rem;
    border: 0.1rem solid $neutral_200;
    border-radius: 0.2rem;
}
.groupSettingsModal_defaultLine {
    position: absolute;
    top: 0;
    left: -0.1rem;
    width: 0.2rem;
    height: 100%;
    background: $black;
    border-radius: 0.2rem;
}
.groupSettingsModal_defaultContent {
    display: flex;
    padding: 0 1.4rem;
    padding-top: 1.2rem;
    min-height: 4.8rem;
    flex-wrap: wrap;
    > div {
        margin-bottom: 1.2rem;
        margin-right: 1.2rem;
        border-radius: 0.4rem;
    }
    .groupSettingsModal_defaultCodeWrap {
        display: flex;
        align-items: center;
        padding: 0 0.8rem;
        max-width: 12rem;
        height: 2.4rem;
        background: $background;
        border: 0.1rem solid $neutral_300;
        border-radius: 0.4rem;
        cursor: pointer;
        user-select: none;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }
}
.groupSettingsModal_options_gray {
    > span {
        visibility: hidden;
    }
}

.groupSettingsModal_options_move {
    > span {
        display: inline-block;
        padding: 0 0.8rem;
        background: #ddd;
        border-radius: 0.4rem;
        color: #ddd;
    }
}
.roupSettings_defaultTitle {
    @include subtitle_3();
    display: flex;
    align-items: center;
    padding-left: 1.2rem;
    height: 3.2rem;
    line-height: 3.2rem;
    color: $neutral_500;
    background: rgba($black, 0.1);
    &:hover {
        .groupItem_setting {
            display: flex;
        }
    }
}
.groupSettingsModal_dragArea {
    margin-bottom: 1.6rem;
    width: 100%;
    height: 3.4rem;
    border: 0.2rem dashed #ccc;
    border-radius: 0.2rem;
}
.groupSettingsModal_addButton {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 3.2rem;
    color: $primary_600;
    background: $bg;
    border: 0.1rem solid $neutral_200;
    border-left: none;
    border-right: none;
    cursor: pointer;
    > span {
        @include body_2();
        line-height: 1.6rem;
    }
    svg {
        margin-right: 1rem;
        font-size: 1.6rem;
    }
}
.groupSettingsModal_btn {
    position: absolute;
    bottom: 1.6rem;
    right: 2.4rem;
    button {
        &:nth-of-type(1) {
            color: $primary_600 !important;
        }
        &:nth-of-type(2) {
            background: $primary_600;
            box-shadow: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
            border-radius: 0.6rem;
        }
    }
}
.groupSettingsModal_optionsScroll {
    padding-right: 1rem;
}
.moveItem {
    position: fixed;
    padding: 0 0.8rem;
    max-width: 12rem;
    height: 2.4rem;
    background: $background;
    z-index: 100;
    box-shadow: $shadow_small;
    cursor: pointer;
    border-radius: 0.4rem;
    pointer-events: none;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.moveGroupItem {
    position: fixed;
    width: 50.4rem;
    height: 3.2rem;
    background: $background;
    z-index: 100;
    box-shadow: $shadow_small;
    cursor: pointer;
    border-radius: 0.4rem;
    pointer-events: none;
    overflow: hidden;
}
.groupSettingsModal_hint {
    .kite_body {
        padding-right: 0.4rem;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: auto;
        font-size: 1.2rem;
        user-select: none;
        height: auto;
        > span {
            line-height: 1.6rem;
            user-select: none;
        }
        svg {
            margin-right: 0.4rem;
            font-size: 1.6rem;
            user-select: none;
        }
    }
}

.relationItem_scroll {
    .scroll_scrollBar__vertical {
        width: 0.2rem;
        background-color: $neutral_400;
    }
}
.relationItem_scrollBody {
    min-width: 6rem;
    max-width: 27.2rem;
    max-height: 6.8rem;
    overflow-wrap: break-word;
    padding-right: 0.6rem;
}
/* <--------------------------- * SECTION1 * --------------------------- */
