/* <--------------------------- * SECTION1 * --------------------------- */
.executionOptionItem_scriptItem {
    background: $type;
    cursor: pointer;
}
.executionOptionItem_nodesContainer {
    position: relative;
    height: 4rem;
    white-space: nowrap;
    background: $background;
    border-bottom: 0.1rem solid $line_light;
}
.executionOptionItem_dragging {
    background: white;
    box-shadow: $shadow_large;
    border: none !important;
}
.executionOptionItem_isDragging {
    opacity: 0.3;
}
.executionOptionItem_buttonContainer {
    display: inline-block;
    height: 4rem;
    line-height: 4rem;
    color: $line_light;
    border-right: 0.1rem solid $line_light;
    padding-right: 1.4rem;
}

.executionOptionItem_button {
    display: inline-block;
    cursor: pointer;
    margin-left: 1.4rem;
}
.executionOptionItem_contentContainer {
    display: inline-block;
    vertical-align: top;
    width: calc(100% - 9rem);
    height: 4rem;
    line-height: 4rem;
    position: relative;
    padding-left: 1.3rem;
    > input {
        background: none;
        width: calc(100% - 15rem);
        height: 3.8rem;
    }
    > span {
        display: none;
        color: $primary_600;
        margin-right: 1.3rem;
        cursor: pointer;
    }
    &:hover {
        > span {
            display: inline;
        }
        > input {
            width: calc(100% - 17.7rem);
        }
    }
}
.executionOptionItem_jumpButton {
    font: $fs_m_14;
    color: $background;
    user-select: none;
    text-align: center;
    position: absolute;
    right: 2.1rem;
    top: 0.7rem;
    width: 11.8rem;
    height: 2.6rem;
    line-height: 2.6rem;
    background: $primary_600;
    border-radius: 0.4rem;
    cursor: pointer;
    > span:first-child {
        position: absolute;
        left: -4.5rem;
        color: $gray_4;
        font-size: 2.3rem;
        > span {
            font: $fs_r_10;
            color: $type;
            position: absolute;
            bottom: 0.2rem;
            right: -1rem;
            background: $line_light;
            height: 1rem;
            line-height: 1rem;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
