/* <--------------------------- * SECTION1 * --------------------------- */
.markOptionItem_nodesItem {
    cursor: pointer;
}
.markOptionItem_nodesContainer {
    height: 4rem;
    white-space: nowrap;
    border-bottom: 0.1rem solid $line_light;
}
.markOptionItem_buttonContainer {
    display: inline-flex;
    justify-content: space-between;
    width: 7.5rem;
    height: 4rem;
    line-height: 4rem;
    color: $line_light;
    border-right: 0.1rem solid $line_light;
    padding: 0 1rem;
    box-sizing: border-box;
    svg {
        font-size: 1.6rem;
    }
}
.markOptionItem_dragging {
    background: white;
    box-shadow: $shadow_large;
    border: none !important;
}
.markOptionItem_isDragging {
    opacity: 0.3;
}
.markOptionItem_button {
    display: inline-block;
    cursor: pointer;
}
.markOptionItem_contentContainer {
    display: inline-block;
    vertical-align: top;
    width: calc(100% - 9rem);
    height: 4rem;
    line-height: 4rem;
    position: relative;
    padding-left: 1.3rem;
    > input {
        background: none;
        width: calc(100% - 1rem);
        height: 3.8rem;
        outline: none;
        border: none;
    }
    > span {
        display: none;
        color: $primary_600;
        margin-right: 1.3rem;
        cursor: pointer;
    }
    &:hover {
        > span {
            display: inline;
        }
        > input {
            width: calc(100% - 3.7rem);
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
