/* <--------------------------- * SECTION1 * --------------------------- */
.pluginItem_pluginItem {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 10.8rem;
    overflow: hidden;
    border: 0.1rem solid $neutral_200;
    box-sizing: border-box;
    border-radius: 0.6rem;
    &:nth-of-type(n + 2) {
        margin-top: 0.8rem;
    }
    img {
        width: 5rem;
        height: 5rem;
        margin-left: 1rem;
        margin-right: 1.6rem;
        border-radius: 0.6rem;
    }
    &:hover {
        border-style: none;
        border-radius: 1rem;
        box-shadow: $shadow_small;
        .pluginItem_pluginInfo_enter {
            display: block;
        }
        .pluginItem_pluginInfo_price {
            display: none;
        }
    }
}
.pluginItem_pluginInfo {
    width: 100%;
}
.pluginItem_pluginInfo_title {
    @include subtitle_2();
    display: flex;
    justify-content: space-between;
    width: 100%;
    color: $neutral_600;
    .pluginItem_pluginInfo_enter {
        display: none;
        width: 2.4rem;
        height: 2.4rem;
        text-align: center;
        margin-right: 1.3rem;
        color: $neutral_400;
        cursor: pointer;
        &:hover {
            background: $neutral_50;
        }
        svg {
            font-size: 1.6rem;
        }
    }
    .pluginItem_pluginInfo_price {
        @include body_2();
        margin-right: 1.3rem;
        color: #ff6430;
    }
}
.pluginItem_pluginInfo_keyword {
    display: flex;
    span {
        @include label_2();
        display: inline-block;
        margin: 0.7rem 0;
        padding: 0 0.4rem;
        color: $primary_500;
        background: $primary_50;
        border-radius: 1.5rem;
        &:nth-of-type(n + 2) {
            margin-left: 0.4rem;
        }
    }
}
.pluginItem_pluginInfo_description {
    @include label();
    color: $neutral_400;
}

.pluginItem_star {
    position: absolute;
    top: -0.7rem;
    left: -2.5rem;
    width: 7.69rem;
    height: 3.999rem;
    background: $neutral_100;
    transform: rotate(-45deg);
    text-align: center;
    svg {
        font-size: 1.4rem;
        margin-top: 1.5rem;
    }
    &__starred {
        background: $yellow_50;
    }
}

.pluginItem_star__collection {
    font-size: 1.8rem;
    color: $yellow;
    cursor: pointer;
}
.pluginItem_star__unCollection {
    font-size: 1.8rem;
    color: $line_light;
    cursor: pointer;
    &:hover {
        color: $yellow;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
