/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */

/** Import all the reference constant after this line */

@import '~/Constant/base.scss';
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */

/** The demo mixin is ..........*/
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
@mixin toolTips {
    .ToolTips_container {
        display: flex;
        justify-content: center;
        align-items: center;
        user-select: none;
    }
    > .navigation_toolTips {
        display: none;
        position: absolute;
        left: 0;
        top: -1.2rem;
        transform: translateX(-50%);
        margin-left: 0.8rem;
        margin-bottom: 3rem;
    }
    > svg:hover + .navigation_toolTips {
        display: block;
    }
}

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.navigation_container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 4.8rem;
    width: 100%;
    background: $primary_600;
    @media screen and (max-width: 1023px) {
        display: none;
    }
}

.navigation_leftContainer {
    display: none;
    @media screen and (min-width: 1024px) {
        display: block;
        padding-left: 2rem;
    }
}

.navigation_headerItem {
    display: inline-block;
    position: relative;
    height: 2.4rem;
    margin-right: 2.5rem;
    padding: 0 2rem;
    font: $fs_m_14;
    line-height: 2.4rem;
    text-align: center;
    color: $background;
    user-select: none;
    cursor: pointer;
    outline: none;
}

.navigation_headerItem__select {
    display: inline-block;
    position: relative;
    height: 2.4rem;
    margin-right: 2.5rem;
    padding: 0 2rem;
    font: $fs_m_14;
    line-height: 2.4rem;
    text-align: center;
    color: $primary_600;
    background-color: $background;
    border-radius: 5rem;
    user-select: none;
    cursor: pointer;
    outline: none;
}

.navigation_headerItem:hover {
    color: $line_light;
    transition: 0.2s;
}

.navigation_headerRight {
    display: flex;
    align-items: center;
    height: 4.8rem;
    margin-right: 0.5rem;
    svg {
        font-size: 1.6rem;
    }
}
.navigation_headerRight_taskReporting {
    .kite_body {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 12rem;
        height: 2.5rem !important;
        text-align: center;
    }
}
.navigation_headerRight_share {
    .kite_body {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 5rem;
        height: 2.5rem !important;
        text-align: center;
    }
}
.navigation_headerFile {
    position: absolute;
    border-radius: 0.6rem;
    text-align: left;
    left: -2rem;
    margin-top: 1.2rem;
    z-index: 77;
    svg {
        font-size: 1.6rem;
    }
}
.navigation_headerRight > div {
    position: relative;
    display: inline-flex;
    height: 4.8rem;
    margin-right: 2.5rem;
    align-items: center;
    cursor: pointer;
    color: $background;
    @include toolTips();
}
/* <--------------------------- * SECTION1 * --------------------------- */
