.quotaOptions_firstAdd {
    height: 13.7rem;
    padding-top: 1.1rem;
    padding-left: 1.4rem;
    font: $fs_r_16;
    color: $primary_600;
    cursor: pointer;
    > span {
        color: $primary_600;
        font-size: 1.5rem;
        margin-right: 1.2rem;
    }
}
.quotaOptions_moveGroupItem {
    position: fixed;
    background: $background;
    z-index: 100;
    box-shadow: $shadow_small;
    cursor: pointer;
    pointer-events: none;
    overflow: hidden;
    .questionOptionItem_questionList_item {
        border-bottom: none !important;
    }
    .reusableList_container {
        border-bottom: none !important;
    }
}
.quotaOptions_dragArea {
    width: 100%;
    height: 4rem;
    border: 0.2rem dashed #ccc;
    border-radius: 0.2rem;
}
