/* <--------------------------- * SECTION1 * --------------------------- */
.questionNodeEdit_questionTextContainer {
    min-height: 20rem;
    border-bottom: 0.1rem solid $line_light;
    > div:first-child {
        font: $fs_r_20;
        padding-left: 2.5rem;
        height: 5.1rem;
        line-height: 5.1rem;
        border-bottom: 0.1rem dashed $line_light;
    }
}
.questionNodeEdit_questionTextEdit {
    > div {
        min-height: 20rem !important;
    }
}

.questionNodeEdit_nodesTag {
    display: flex;
    min-height: 8rem;
}

.questionNodeEdit_optionInstruction {
    display: inline-block;
    width: calc(100% - 12.5rem);
    min-height: 8rem;
    border-left: 0.1rem solid $line_light;
    > div:first-child {
        height: 4rem;
        line-height: 4rem;
        padding-left: 1rem;
        position: relative;
        border-bottom: 0.1rem solid $line_light;
    }
    .questionNodeEdit_optionInstructionEdit {
        position: relative;
        min-height: 4rem;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
