/* <--------------------------- * SECTION1 * --------------------------- */
.groups_container {
    min-height: 50rem;
}
.groups_row_title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1.8rem;
    height: 4rem;
    border-bottom: 0.1rem solid $line_light;
}
.groups_row_name {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $neutral_600;
}
.groups_row_setting {
    color: $primary_600;
    cursor: pointer;
    svg {
        margin-right: 0.8rem;
        font-size: 1.6rem;
    }
}
.groups_row_settingContent {
    @include body_2();
    color: $primary_600;
}
.groups_groupItem {
    position: relative;
    margin-bottom: 0.2rem;
    .questionOptionItem_optionSetting {
        display: none;
    }
    box-sizing: border-box;
}
.groups_groupItem_line {
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% - 0.3rem);
    height: calc(100% - 0.2rem);
    border-width: 0.2rem;
    border-style: solid;
    z-index: 2;
}
.groups_groupItem_name {
    position: absolute;
    top: 0;
    left: 50%;
    padding: 0 1rem;
    height: 2rem;
    line-height: 2rem;
    transform: translate(-50%, -50%);
    background: $background;
    z-index: 3;
    border-radius: 0.4rem;
}
/* <--------------------------- * SECTION1 * --------------------------- */
