/* <--------------------------- * SECTION1 * --------------------------- */

.deleteNodeModal_container {
    position: relative;
    user-select: none;
    width: 35rem;
    height: 20rem;
    padding-top: 2rem;
    background: $background;
    border-radius: 0.6rem;
    > div:first-child {
        color: $primary_600;
        font-size: 2.4rem;
        margin-left: 1.5rem;
        display: flex;
        align-items: center;
        > span {
            font: $fs_m_14;
            color: $gray_1;
            margin-left: 1rem;
        }
    }
}
.deleteNodeModal_content {
    margin-left: 4.9rem;
    margin-top: 1.2rem;
    padding-right: 2.4rem;
    font: $fs_r_14;
    line-height: 2rem;
    > span:nth-of-type(2) {
        color: #212121;
    }
}
.deleteNodeModal_btn {
    position: absolute;
    right: 2.4rem;
    bottom: 2.4rem;
    width: 14.8rem;
    display: flex;
    justify-content: space-between;
    button {
        font-size: 14px !important;
    }
    .button_button__primary {
        box-shadow: none;
        background: #22a6b3;
    }
    .button_button__secondary {
        border: none;
        color: #757575;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
