/* <--------------------------- * SECTION1 * --------------------------- */
.customScriptHead_title {
    display: flex;
    justify-content: space-between;
    width: 100%;
    position: relative;
    border-bottom: 0.1rem solid $neutral_200;
    font: $fs_r_12;
    .customScriptHead_scriptIcon {
        position: relative;
        display: inline-block;
        width: 5.2rem;
        height: 3.7rem;
        line-height: 3.7rem;
        text-align: center;
        border-right: 1px solid #ebebeb;
    }
    .customScriptHead_scriptTitle {
        display: inline-block;
        height: 3.7rem;
        line-height: 3.7rem;
        width: 10.2rem;
        text-align: center;
        border-right: 1px solid #ebebeb;
    }
    .customScriptHead_scriptName {
        display: flex;
        align-items: center;
        flex: 1;
        padding-left: 1rem;
        > input {
            display: inline-block;
            line-height: 3.7rem;
            height: 3.7rem;
            text-align: left;
            width: 10.8rem;
            padding-left: 1rem;
            padding-right: 1rem;
        }
        .customScriptHead_label_draft {
            @include label_2();
            font-size: 1.2rem;
            line-height: 3.7rem;
            padding: 0 0.4rem;
            color: $yellow_600;
            line-height: 1.4rem;
            text-align: center;
            background: $yellow_50;
            border: 0.1rem solid $yellow_600;
            border-radius: 0.4rem;
            cursor: pointer;
            > span {
                transform: scale(0.833);
                margin-right: 0;
            }
        }
        .customScriptHead_label_verified {
            @include label_2();
            font-size: 1.2rem;
            line-height: 3.7rem;
            padding: 0 0.4rem;
            color: $success_600;
            line-height: 1.4rem;
            text-align: center;
            background: $success_100;
            border: 0.1rem solid $success_600;
            border-radius: 0.4rem;
            cursor: pointer;
            > span {
                transform: scale(0.833);
                margin-right: 0;
            }
        }
    }

    .customScriptHead_test {
        @include body_2();
        display: flex;
        align-items: center;
        margin-right: 1.2rem;
        color: $primary_600;
        cursor: pointer;
        svg {
            margin-right: 0.5rem;
            font-size: 1.6rem;
        }
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */
