/* <--------------------------- * SECTION1 * --------------------------- */
.collapse_panel_select {
    .collapse_header {
        .collapse_hederContent {
            color: $primary_600;
        }
    }
    .collapse_content {
        border-bottom: 1px solid $neutral_200;
        &:empty {
            border-bottom: none !important;
        }
    }
}
.collapse_header {
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 1rem;
    height: 4rem;
    border-bottom: 1px solid $neutral_200;
    user-select: none;
    cursor: pointer;
    .collapse_arrow {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 1.1rem;
        transition: transform 0.2s ease;
        svg {
            font-size: 0.9rem;
        }
    }
    .collapse_titleIcon {
        font-size: 1.8rem;
        margin-right: 0.8rem;
        line-height: 0;
        color: $primary_600;
    }
    .collapse_hederContent {
        display: flex;
        align-items: center;
        @include body_2();
        margin-left: 0.8rem;
        color: $neutral_500;
    }
    .collapse_LabelAddition {
        display: flex;
        align-items: center;
        height: 4rem;
        color: $primary_600;
        font-size: 1.8rem;
        position: absolute;
        top: 0;
        right: 1.2rem;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
