/* <--------------------------- * SECTION1 * --------------------------- */
.loopOptionItem_nodesContainerLoop {
    display: flex;
    min-height: 4rem;
    background: $background;
    border-bottom: 0.1rem solid $line_light;
    white-space: nowrap;
}
.loopOptionItem_dragging {
    background: white;
    box-shadow: $shadow_large;
    border: none !important;
}
.loopOptionItem_isDragging {
    opacity: 0.3;
}
.loopOptionItem_optionButtonContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 7.5rem;
    min-height: 4rem;
    padding: 0 1rem;
    font-size: 1.6rem;
    color: $line_light;
    border-right: 0.1rem solid $line_light;
    box-sizing: border-box;
    svg {
        cursor: pointer;
    }
    span {
        display: flex;
        align-items: center;
        height: 100%;
    }
}
.loopOptionItem_optionContentContainer {
    display: inline-block;
    width: calc(100% - 13rem);
}
/* <--------------------------- * SECTION1 * --------------------------- */
