/* <--------------------------- * SECTION1 * --------------------------- */
.nodeTitle_container {
    position: relative;
    display: flex;
    width: 100%;
    background: $background;
    border-bottom: 0.1rem solid $line_light;
}

.nodeTitle_box {
    display: inline-flex;
    align-items: center;
    padding: 0 1rem;
    width: 100%;
    height: 3.7rem;
    font: $fs_r_14;
    input {
        font: $fs_r_14;
    }
}

.nodeTitle_input {
    padding-top: 0;
    padding-bottom: 0;
    width: 100%;
    text-indent: 0;
    border: none;
    outline: none;
}

.nodeTitle_icon {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    min-width: 5rem;
    height: 3.7rem;
    font-size: 1.6rem;
    color: $primary_600;
    border-right: 0.1rem solid $line_light;
}

.nodeTitle_content {
    display: inline-block;
    height: 3.7rem;
    line-height: 3.7rem;
    padding-left: 1rem;
}
/* <--------------------------- * SECTION1 * --------------------------- */
