/* <--------------------------- * SECTION1 * --------------------------- */
.reusableEdit_contentContainer {
    position: relative;
    width: 100%;
    box-shadow: $shadow_01dp;
    position: relative;
    background: $background;
    z-index: 11;
    display: inline-block;
}
.reusableEdit_titleContainer {
    display: flex;
    width: 100%;
    position: relative;
    border-bottom: 0.1rem solid $line_light;
    input {
        border: none;
        outline: none;
    }
    svg {
        font-size: 1.6rem;
    }
    > div:first-child {
        display: inline-block;
        width: 5rem;
        height: 3.7rem;
        border-right: 0.1rem solid $line_light;
        line-height: 3.7rem;
        text-align: center;
        color: $primary_600;
    }
    > input,
    > span {
        font: $fs_r_12;
        color: $type;
        display: inline-block;
        border-right: 0.1rem solid $line_light;
        text-align: left;
        padding-left: 1rem;
        width: 100%;
        height: 3.7rem;
        line-height: 3.7rem;
        vertical-align: top;
    }
    > span {
        overflow-y: scroll;
        &::-webkit-scrollbar {
            width: 0;
            height: 0;
        }
    }
}
.reusableEdit_textContainer {
    height: 33.7rem;
    border-bottom: 0.1rem solid $line_light;
    > div:first-child {
        display: flex;
        align-items: center;
        padding-right: 2.5rem;
        padding-left: 2.5rem;
        height: 4.9rem;
        border-bottom: 0.1rem dashed $line_light;
        span {
            @include h_4();
            font: $fs_r_20;
            color: $primary_600;
        }
        svg {
            margin-left: 1rem;
            font-size: 1.6rem;
            color: $neutral_200;
        }
    }
}
.reusableEdit_hint {
    .kite_body {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 26.4rem;
        height: 9.6rem !important;
        text-align: left;
        @include body_2();
    }
}

.reusableEdit_optionAdd {
    height: 4rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    > span:first-child {
        color: $primary_600;
        font-size: 1.8rem;
        margin-left: 1.3rem;
        margin-right: 0.8rem;
    }
    > span:last-child {
        font: $fs_r_12;
        color: $line_light;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
