/* <--------------------------- * SECTION1 * --------------------------- */
.nodeTreeItem_item {
    position: relative;
    display: flex;
    align-items: center;
    padding-right: 1rem;
    width: 100%;
    height: 4.4rem;
    cursor: pointer;
    user-select: none;
    border: 0.1rem solid transparent;
    background: $background;
    transition: background 0.5s ease;
    box-sizing: border-box;
    &_arrow {
        display: flex;
        align-items: center;
        margin-right: 0.5rem;
        width: 1rem;
        font-size: 0.8rem;
        svg {
            transition: transform 0.2s ease;
        }
    }
    &_icon {
        display: flex;
        align-items: center;
        width: 2rem;
        margin-right: 1rem;
        color: $primary_600;
        font-size: 1.4rem;
    }
    &_content {
        flex: 1;
        @include body_2();
        color: $neutral_600;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    &_more {
        display: none;
        height: 4.4rem;
        &__select {
            display: flex;
            align-items: center;
            color: $primary_600;
        }
    }
    &_ident {
        width: 1rem;
    }
    &__select {
        background: rgba($primary_variant, 0.07);
    }
    &:hover {
        background: rgba($primary_variant, 0.07);
        transition: 0.2s;
        .nodeTreeItem_item_more {
            display: flex;
            align-items: center;
            color: $primary_600;
        }
        .nodeTreeItem_item_line_after {
            display: block;
        }
        .nodeTreeItem_item_line_before {
            display: block;
        }
    }
}
.nodeTreeItem_Icon_tip {
    .kite_body {
        width: 2rem;
        line-height: 1.4rem;
        white-space: nowrap;
        width: auto !important;
        height: 3rem !important;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
