/* <--------------------------- * SECTION1 * --------------------------- */
.relationCard_container {
    width: 100%;
    height: calc(100vh - 30rem);
    overflow: hidden;
    background: radial-gradient(rgb(80, 80, 80) 0.5px, rgb(255, 255, 255) 0.5px) 0% 0% / 15px 15px
        rgb(47, 49, 54);
}
.relationCard_content {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    &_drag {
        cursor: move;
    }
}
.relationCard_rect {
    stroke: #475872;
}
.relation_left {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
    width: 30rem;
    height: 30rem;
    transform: translateX(-20rem);
    .relation_node {
        > div:nth-of-type(1) {
            &::after {
                position: absolute;
                top: 0;
                right: 0;
                display: block;
                content: '';
                width: 20rem;
                height: 1px;
                transform: translate(100%, 1.2rem);
                background: $primary_600;
            }
        }
    }
}
.relation_left__center {
    justify-content: center !important;
}
.relation_node {
    position: relative;
    max-width: 17.1rem;
    height: 2.8rem;
    cursor: pointer;
    &:hover {
        .relation_tips {
            display: block;
        }
    }
    > div:nth-of-type(1) {
        display: inline-flex;
        align-items: center;
        padding-right: 1rem;
        max-width: 17.1rem;
        height: 2.8rem;
        line-height: 2.8rem;
        border: 1px solid $primary_600;
        box-sizing: border-box;
        border-radius: 6px;
        user-select: none;
        > svg {
            margin-left: 0.9rem;
            font-size: 1.2rem;
            color: $primary_600;
        }
        > span {
            display: inline-block;
            max-width: 13rem;
            margin-left: 0.8rem;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            color: $primary_600;
        }
    }
}
.relation_line {
    width: 1px;
    height: 27.3rem;
    transform: translateY(-0.1rem);
    background: $primary_600;
}
.relation_right {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 30rem;
    height: 30rem;
    transform: translateX(20rem);
    .relation_node {
        > div:nth-of-type(1) {
            &::after {
                position: absolute;
                top: 0;
                left: 0;
                display: block;
                content: '';
                width: 20rem;
                height: 1px;
                transform: translate(-100%, 1.2rem);
                background: $primary_600;
            }
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
