/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@import '~/Constant/base.scss';
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/
@mixin demo {
}
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.QuestionNode_optionDimContainer {
    position: relative;
    display: flex;
    height: 3.7rem;
    font: $fs_r_14;
    color: $type;
    line-height: 3.7rem;
    &::after {
        content: '';
        position: absolute;
        bottom: -0.1rem;
        width: 100%;
        height: 0.1rem;
        background: $line_light;
    }
    &::before {
        content: '';
        position: absolute;
        top: 0;
        width: 100%;
        height: 0.1rem;
        // transform: translateY(-0.1rem);
        background: $line_light;
    }
}

.QuestionNode_optionDimPty {
    position: relative;
    flex: 1;
    display: inline-flex;
    justify-content: space-between;
    height: 3.6rem;
    .check_content {
        @include body_2();
        color: $neutral_400;
    }
    .check_checkIcon {
        color: $success_600;
    }
    .QuestionNode_checked {
        color: $success_600;
    }
    .QuestionNode_check {
        display: inline-flex;
        align-items: center;
        > span {
            display: inline-block;
            margin-left: 1rem;
        }
    }
    .check_icon {
        margin-right: 0;
    }
}

.QuestionNode_tip {
    .kite_body {
        @include body_3();
        display: flex;
        align-items: center;
        width: 21.2rem;
        height: 5.8rem;
        font-size: 1.2rem;
        > span {
            line-height: 1.6rem;
        }
        svg {
            margin-right: 0.4rem;
            font-size: 1.6rem;
        }
    }
}
.QuestionNode_optionButtons {
    margin-right: 2.05rem;
}
.QuestionNode_optionDimLabel {
    display: inline-block;
    margin-left: 1.6rem;
    margin-right: 2.6rem;
    font-size: 1.2rem;
    text-align: center;
}

.QuestionNode_contentConfig {
    display: inline-block;
    width: 4rem;
    text-align: right;
    font-size: 1.6rem;
    color: $primary_700;
    cursor: pointer;
    vertical-align: top;
}

.QuestionNode_optionDimIcon {
    display: inline-block;
    margin-left: 1.2rem;
    color: $line_light;
    font-size: 1.6rem;
    cursor: default;
    position: relative;
    &:hover {
        .QuestionNode_optionDimHover {
            display: block;
        }
    }
}

.QuestionNode_optionDimHover {
    display: none;
    position: absolute;
    top: -3.5rem;
    left: -2.5rem;
    font: $fs_r_14;
    width: 6.5rem;
    height: 3.8rem;
    text-align: center;
    line-height: 3.8rem;
    color: $background;
    border-radius: 0.2rem;
    background: rgba($color: $type, $alpha: 0.75);
}

.QuestionNode_optionDimHover::after {
    content: '';
    position: absolute;
    width: 0.5rem;
    height: 0.5rem;
    top: calc(3.8rem - 0.25rem);
    left: 3.1rem;
    background: rgba(0, 0, 0, 0.75);
    transform: matrix(-0.71, -0.71, -0.71, 0.71, 0, 0);
}

.QuestionNode_additionDimContainer {
    display: inline-block;
    position: absolute;
    left: -2.5rem;
}

.QuestionNode_additionOptionContainer {
    display: inline-block;
    position: absolute;
    left: -7.5rem;
}
.QuestionNode_import {
    display: inline-block;
    margin-left: 1rem;
    cursor: pointer;
    svg {
        font-size: 1.4rem;
        color: $primary_600;
    }
}
.QuestionNode_import_tip {
    .kite_body {
        display: flex;
        align-items: center;
        width: 13.4rem;
        height: 2.4rem;
    }
}
.QuestionNode_save {
    display: inline-block;
    margin-left: 1rem;
    cursor: pointer;
    svg {
        font-size: 1.4rem;
        color: $primary_600;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
