/* <--------------------------- * SECTION1 * --------------------------- */
.root {
    position: relative;
    height: calc(100vh - 24.3rem);
}
.navTag_container {
    padding-left: 12rem;
    padding-right: 2rem;
    height: calc(100vh - 24.3rem);
    .navTag_tags {
        position: absolute;
        top: 0rem;
        left: 2rem;
        background: $background;
        z-index: 10;
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */

.navTag_mainContainer {
    user-select: none;
    position: relative;
    > div:first-child {
        position: fixed;
        top: 20.5rem;
        left: 20.3rem;
    }
}

.navTag_shadow {
    position: relative;
    &::after {
        content: '';
        display: block;
        width: 100%;
        position: absolute;
        top: 0px;
        left: 0;
        height: 10px;
        box-shadow: 0px -8px 8px -8px inset rgb(104, 104, 104);
        transform: rotateX(180deg);
        z-index: 12;
    }
}
.navTag_contentContainer_scroll {
    position: relative;
    width: 100%;
    height: calc(100vh - 24.3rem);
    overflow-y: scroll;
    padding: 0.5rem;
    padding-top: 0;
    margin: -0.5rem;
    margin-top: 0;
    &::-webkit-scrollbar {
        display: none;
    }
}
.navTag_contentShadow {
    position: absolute;
    top: 0rem;
    left: 0.5rem;
    width: calc(100% - 1rem);
}
.navTag_contentContainer {
    position: relative;
    width: 100%;
    height: auto;
    min-height: 20rem;
    border: 0.1rem solid $primary_600;
    background: $background;
    z-index: 11;
    display: inline-block;
    &::after {
        content: '';
        position: absolute;
        bottom: -0.1rem;
        width: 100%;
        height: 0.1rem;
        background: $primary_600;
    }
}

.navTag_tag {
    width: 10rem;
    background: $not_activated;
    margin-bottom: 0.3rem;
    height: 3.2rem;
    display: flex;
    position: relative;
    font: $fs_m_14;
    justify-content: space-between;
    align-items: center;
    color: $primary_600;
    padding-left: 0.8rem;
    padding-right: 0.2rem;
    left: 7rem;
    border-radius: 0.1rem;
    transition: 0.4s left ease-in;
    &:hover {
        left: -1rem;
        transition: 0.4s left ease-out;
    }
    cursor: pointer;
    > span {
        font-size: 1.6rem;
    }
}

.navTag_tag_selected {
    background: $primary_600;
    color: $background;
    left: -1rem;
}
.navTag_collaboration {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    background: $gray_5;
    opacity: 0.2;
}

/* <--------------------------- * SECTION1 * --------------------------- */
