@import '@datareachable/dr_front_componentlibrary/Constant/breakPoint.scss';
@import '@datareachable/dr_front_componentlibrary/Constant/font.scss';
@import '@datareachable/dr_front_componentlibrary/Constant/color.scss';

$shadow_mini: 0px 4px 5px -2px rgba(26, 26, 26, 0.08), 0px 2px 3px rgba(26, 26, 26, 0.06);
$shadow_small: 0px 2px 8px rgba(26, 26, 26, 0.08), 0px 2px 4px rgba(26, 26, 26, 0.03);
$shadow_regular: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
$shadow_medium: 0px 4px 6px rgba(26, 26, 26, 0.06), 0px 6px 20px -2px rgba(26, 26, 26, 0.14);
$shadow_large: 0px 16px 24px -2px rgba(26, 26, 26, 0.12), 0px 2px 12px rgba(26, 26, 26, 0.1);
@mixin label_2 {
    font: 400 1rem / 1.6rem $commonFamily;
    letter-spacing: 0.1rem;
}
