/* <--------------------------- * SECTION1 * --------------------------- */
.reusableList_container {
    background: $primary_10;
    user-select: none;
    border-bottom: 0.1rem solid $line_light;
    cursor: default;
}
.reusableList_container_draging {
    opacity: 0.5;
}
.reusableList_head {
    position: relative;
    @include body_2();
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1.6rem;
    height: 3.55rem;
}
.reusableList_drag_icon {
    margin-right: 1rem;
    cursor: grab !important;
    > svg {
        color: $line_light;
        cursor: grab !important;
    }
}
.reusableList_container_hover {
    // box-shadow: $shadow_large;
    .reusableList_container {
        border: none !important;
    }
}
.reusableList_operator {
    display: flex;
    align-items: center;
}
.reusableList_more {
    display: flex;
    align-items: center;
    position: relative;
    margin-right: 2.479rem;
    font-size: 0.5rem;
    cursor: pointer;
}
.reusableList_unfold {
    font-size: 1rem;
    cursor: pointer;
}

.reusableList_item {
    display: flex;
    margin-bottom: -1px;
    height: 4rem;
    border: 0.1rem solid $neutral_300;
    border-left: none;
    border-right: none;
    color: $neutral_300;
}
.reusableList_item_buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    width: 7.5rem;
    height: 3.9rem;
    border-right: 0.1rem solid $neutral_300;
    svg {
        font-size: 1.6rem;
        color: $neutral_300;
    }
}
.reusableList_item_code {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 3.9rem;
    border-right: 0.1rem solid $neutral_300;
}
.reusableList_item_content {
    display: flex;
    align-items: center;
    padding-left: 2.5rem;
}
/* <--------------------------- * SECTION1 * --------------------------- */
