/* <--------------------------- * SECTION1 * --------------------------- */
.dropDownMenu_container {
    display: flex;
}
.dropDownMenu_box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 13.3rem;
    height: 8.8rem;
    padding: 1.2rem 0;
    background: $background;
    box-shadow: $shadow_small;
    cursor: auto;
    border-radius: 0.6rem;
    z-index: 3;
    box-sizing: border-box;
    > div {
        &:nth-of-type(1):hover {
            .dropDownMenu_add {
                display: flex;
            }
        }
        &:nth-of-type(2) {
            color: $danger_600;
        }
    }
}
.dropDownMenu_add {
    position: absolute;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    top: 0;
    left: 100%;
    width: 15.1rem;
    height: 8.8rem;
    padding: 1.2rem 0rem;
    background: $background;
    box-shadow: $shadow_small;
    border-radius: 0.6rem;
    z-index: 1000;
    box-sizing: border-box;
}
.dropDownMenu_box_item {
    @include body_2();
    width: 100%;
    height: 2.8rem;
    padding: 0 0.8rem;
    color: $neutral_500;
    box-sizing: border-box;
    &:hover {
        > span {
            background: $primary_10;
        }
    }
    > span {
        display: block;
        line-height: 2.8rem;
        padding-left: 0.8rem;
        background: transparent;
        border-radius: 6px;
        cursor: pointer;
        user-select: none;
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
